html {
  font-size: 62.5%;
}

body {
  background-color: #eee;
  font-family: Roboto, sans-serif;
  font-size: 1.6rem;
}

header {
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
}

header h1 {
  text-align: center;
  color: #333;
  font-size: 3rem;
}

header img {
  border-radius: 2rem;
  width: 4rem;
  display: block;
}

.display-none {
  display: none !important;
}

.alert {
  color: #fff;
  background: #ff6363;
  border-radius: .5rem;
  margin-top: 1rem;
  margin-left: 50%;
  padding: 1rem;
  position: absolute;
  top: 0;
  transform: translateX(-50%);
}

.form-login {
  background: #fff;
  border-radius: 1rem;
  flex-direction: column;
  gap: 2rem;
  width: 35rem;
  margin: 1rem auto auto;
  padding: 2rem;
  display: flex;
  box-shadow: 0 .3rem 1rem .1rem #ddddddbf;
}

.form-login div {
  flex-direction: column;
  justify-content: start;
  gap: .5rem;
  display: flex;
}

input {
  font-size: inherit;
  border: .1rem solid #555;
  border-radius: .5rem;
  height: 2.7rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

input:hover {
  background-color: #e9e6e6;
}

button {
  font-size: inherit;
  cursor: pointer;
  border-radius: 10rem;
}

.btn {
  color: #fff;
  background: #4e4eff;
  border: none;
  padding: 1.5rem;
}

.btn:hover {
  background: #2626fb;
}

.link {
  color: #888;
  background: #fff;
  border: .2rem solid #888;
  padding: 1.5rem;
}

.link:hover {
  inset: 2.3px solid #333;
}

hr {
  width: 100%;
}

p {
  text-align: center;
}

@media (width <= 400px) {
  html {
    font-size: 125%;
  }
}
